@import url(https://fonts.googleapis.com/css?family=Quicksand:300,300i,400,400i,500,500i,700,700i,900,900i);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500,500i,700,700i,900,900i);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,500,500i,700,700i,900,900i);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:300,300i,400,400i,500,500i,700,700i,900,900i);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button:focus {outline:0;}

:root {
    --dusty: #96858f; 
    --lavendar: #6D7993;
    --gray: #9099A2; 
    --paper: #d5d5d5; 
    --purple: #716d93;
}

.container {
    background: #f8fafc;
    overflow: scroll;
}

.header {
    padding-top: 10px; 
    padding-bottom: 20px; 
    top: 0;
    left: 0;
    right:0;
    background-color: white;
}

.name-header {
    margin-left: 20px;
    margin-bottom: 5px;
    margin-top: 50px;
    font-size: 80px;
    text-align: center;
    font-family: 'Roboto Slab';
    font-weight: 400;
    border: none;
    color: #6D7993;
    color: var(--lavendar);
}

.typer {
    font-family: monospace;
    font-size: 18px;
    color: #9099A2;
    color: var(--gray);  
}

.blinker {
    font-size: 18px; 
    text-align: center;
    margin-bottom: 30px;
    cursor: pointer;
}

.Typist .Cursor {
    display: inline-block; }
    .Typist .Cursor--blinking {
      opacity: 1;
      -webkit-animation: blink 1s linear infinite;
              animation: blink 1s linear infinite; }
  
  @-webkit-keyframes blink {
    0% {
      opacity: 1; }
    50% {
      opacity: 0; }
    100% {
      opacity: 1; } 
    }
  
  @keyframes blink {
    0% {
      opacity: 1; }
    50% {
      opacity: 0; }
    100% {
      opacity: 1; } 
    }

.section-body {
    padding-bottom: 20px;
    padding-left: 40px; 
    padding-right: 40px;  
    background-color: white;  
    color: #6D7993;  
    color: var(--lavendar); 
    font-family: 'Roboto Slab';
    /*max-width: 1000px;*/
    margin-left: 10%;
    margin-right: 10%;  
}

.section-header {
    padding-top: 20px; 
    font-weight: 600; 
    font-size: 18px;
    color: #96858f;
    color: var(--dusty); 
}

.links {
    margin-top: 40px; 
    background: white;
    margin-left: 50px; 
    margin-right: 50px; 
    
}

.icon {
    /*padding-left: 150px;*/
    padding-top: 10px; 
    padding-bottom: 10px;
    width: 30px;
    height: 30px;
    transition: all 0.5s;
    cursor: pointer;
}

.icon-next {
    margin-left: 80px;
    padding-top: 10px; 
    padding-bottom: 10px;
    width: 30px;
    height: 30px;
    transition: all 0.5s;
    cursor: pointer;
}

.images {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.icon-next:hover{
    width: 35px;
    height: 35px;
    padding-top: 5px; 
    padding-bottom: 5px;
    text-decoration: none;
}

.icon-next:hover span:after {
    text-decoration: none;
    padding-top: 10px; 
    padding-bottom: 10px;
    width: 30px;
    height: 30px;
    transition: all 0.5s;
}

.icon:hover{
    width: 35px;
    height: 35px;
    padding-top: 5px; 
    padding-bottom: 5px;
    text-decoration: none;
}

.icon:hover span:after {
    text-decoration: none;
    padding-top: 10px; 
    padding-bottom: 10px;
    width: 30px;
    height: 30px;
    transition: all 0.5s;
}

.footer {
    background-color: white; 
    height: 75px; 
    width: 100%; 
    margin-top: 40px; 
    font-family: 'Roboto Slab';
    color: #9099A2;
    color: var(--gray);
    font-size: 15px;
}

.copyright {
    padding-top: 25px; 
    margin-left: 10%; 
}

.img {
/*    width: 200px;*/
    height: 200px;
    float: center;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border-radius: 15px; 
    margin-top: 20px;
    margin-bottom: 20px;
}

.project-title {
/*    padding-bottom: 10px;*/
    font-size: 18px;
    width: 100%;
    text-align: center;
    font-weight: 600;
    color: var(--lavendar);
}

.project-body {
    font-size: 15px;
    padding-left: 20px;
    padding-right: 20px;
}

.project-container {
    margin-top: 20px;
    border: 1px solid #EBECF0;
    width: 300px;
    margin: 0 auto; 
    margin-bottom: 30px;
    float: left;
    height: 240px; 
    -webkit-flex-direction: column; 
            flex-direction: column;
    margin-left: 5%;
    margin-right: 5%;  
    /*margin-left: 10%;
    margin-right: 10%;  */
    /*margin-left: 20px;*/
    /*margin-right: auto;*/
}

.project-inner {
    transition: all 0.5s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.project-container:hover .project-inner {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.project-front, .project-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.project-back {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.project-links {
    transition: all 0.5s;
    cursor: pointer;
    font-weight: 600;
    color: #4682B4;
    text-decoration: underline;
    margin-top: 0px; 
    margin-left: 20px;
}

.project-links:hover{
    text-decoration: none;
    color: #7285A5;
}

.project-links:hover span:after {
    text-decoration: underline;
    color: #4682B4;
    transition: all 0.5s;
}

.tags {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-top: -20px;
/*    width: 50%;*/
/*    margin-left: auto;*/
/*    margin-right: auto;*/
    margin-left: 10px;
    overflow: scroll;
    overflow-y: hidden;
    width: 90%;
}

.tag {
    padding-right: 10px;
    padding-left: 10px;
    margin-left: 10px;
    margin-right: 10px;
    text-align: center;
    border: 2px solid #4C516D;
    border-radius: 10px;
    background: var(--lavendar);
    color: white;
    font-size: 12px;
    font-weight: 600;
    height: 15px; 
}

.parent {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 100%;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.child{
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

table {
    table-layout: fixed; 
    width: 100%
}

.section-body-project {
    padding-bottom: 20px;
    padding-left: 40px; 
    padding-right: 40px;  
    background-color: white;  
    color: var(--lavendar); 
    font-family: 'Roboto Slab';
    /*max-width: 1000px;*/
    margin-left: 10%;
    margin-right: 10%;  
    
    margin: 10 auto;
    padding: 40; 
    -webkit-justify-content: center; 
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
  
}

